.Home{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin:0;
    padding: 0;
}

.image{
    height:5cm;
    width:5cm;
}

.bg1 {
    width: 100vh;
    height: 50vh;
    background: linear-gradient(to bottom, transparent 50%, rgb(0, 0, 0) 50%),
                radial-gradient(circle closest-side, #e2fb00, rgb(248, 141, 70));
    border: 1px solid black;
    border-radius: 12px;
  }
  
  .bg2 {
    width: 100vh;
    height: 50vh;
    background: linear-gradient(to bottom, transparent 50%, black 50%),
                radial-gradient(circle closest-side, #000000 35%, rgb(39, 39, 43));
    border: 1px solid black;
    border-radius: 12px;
  }

  .toggle {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 1cm;
  }
  .toggle input[type="checkbox"] {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    cursor: pointer;
    opacity: 0;
  }
  .toggle label {
    position: relative;
    display: flex;
    align-items: center;
  }
  .toggle label:before {
    content: '';
    width: 90px;
    height: 42px;
    background: #ccc;
    position: relative;
    display: inline-block;
    border-radius: 46px;
    transition: 0.2s ease-in;
  }
  .toggle label:after {
    content: '';
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    left: 0;
    top: -5px;
    z-index: 2;
    background: #000000;
    box-shadow: 0 0 5px #0002;
    transition: 0.2s ease-in;
  }
  .toggle input[type="checkbox"]:hover + label:after  {
      box-shadow: 0 2px 15px 0 #0002, 0 3px 8px 0 #0001;
  }
  .toggle input[type="checkbox"]:checked + label:before {
    background: #818181;
  }
  .toggle input[type="checkbox"]:checked + label:after {
    background: #000000;
    left: 40px;
  }